            <p>{{userName}} 您好<br /><span>12:23PM</span></p>
        </router-link>
    </nav>
</template>
<script>
import { mapGetters } from 'vuex'
  export default {
    computed: {
      ...mapGetters([
        'User'
      ]),
      isLogin () {
        return this.User.isLogin;
      },
      userName () {
        return this.User.name;
      }
    },
    methods: {
      logout(){
        localStorage.removeItem('token');
        this.$router.push('/login');
        //清空state裡面的值
        this.$store.commit({
          type: 'clearUserData'
        });
      }
    }
  }
</script>

<style lang="scss">
nav {
  button {
    position: absolute;
    right: 0;
  }
}
</style>



